<template>
	<vui-card v-bind:bordered="false" shadow="always" class="margin-top-20" bodyStyle="padding: 0;">
		<template slot="title"></template>
		<vui-table v-bind:loading="loading" v-bind:columns="columns" v-bind:data="data" rowKey="id" class="vui-pro-dashboard-workplace-todo-list">
			<template slot="no" slot-scope="{ row, rowIndex }">{{rowIndex + 1}}</template>
			<template slot="title" slot-scope="{ row, rowIndex }">{{row.title}}</template>
			<template slot="collaborators" slot-scope="{ row, rowIndex }">
				<vui-avatar-group size="small">
					<vui-tooltip v-for="collaborator in row.collaborators" v-bind:key="collaborator.id" v-bind:content="collaborator.name">
						<vui-avatar v-bind:src="collaborator.avatar" />
					</vui-tooltip>
				</vui-avatar-group>
			</template>
		</vui-table>
	</vui-card>
</template>

<script>
	export default {
		data() {
			return {
				loading: false,
				columns: [
					{ key: "no", slot: "no", width: 60, align: "center", ellipsis: true, title: "#" },
					{ key: "title", dataIndex: "title", slot: "title", ellipsis: true, title: "" },
					{ key: "collaborators", dataIndex: "collaborators", width: 100, ellipsis: true, slot: "collaborators", title: "" }
				],
				data: [
					{
						id: 3,
						title: " Notion  BRNR ",
						collaborators: [
							{ id: 1, avatar: "/static/images/avatars/png/1.png", name: "" },
							{ id: 2, avatar: "/static/images/avatars/png/2.png", name: "" }
						]
					},
					{
						id: 2,
						title: " CDN ",
						collaborators: [
							{ id: 3, avatar: "/static/images/avatars/png/3.png", name: "" },
						]
					},
					{
						id: 1,
						title: " API  Demo Code",
						collaborators: [
							{ id: 3, avatar: "/static/images/avatars/png/3.png", name: "" },
							{ id: 4, avatar: "/static/images/avatars/png/4.png", name: "" }
						]
					}
				]
			};
		}
	};
</script>

<style>
	.vui-pro-dashboard-workplace-todo-list .vui-table-body tr:last-child td { border-bottom:none; }
</style>


